* {
  box-sizing: border-box;
}

.page {
  min-height: 100vh;
  .content-area {
    width: calc(100% - 250px);
    min-height: 100vh;
    transition: 0.3s;
    float: right;
    will-change: width;
    background-color: rgb(208, 215, 255);
    &.no-sidebar {
      width: 100%;
    }
  }
}
